import React, { useState, useEffect } from 'react';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { Typography, Grid, Avatar, TextField, Select, InputLabel, FormControl, MenuItem, Box, Tabs, Tab } from '@mui/material';
import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';

import commands from 'utils/commands.js';

const darkTheme = createTheme({
  palette: {
    mode: 'dark'
  }
});

const accessLevels = [
  { value: 'owners', label: 'Bot Owners' },
  { value: 'admins', label: 'Bot Admins' },
  { value: 'broadcaster', label: 'Broadcaster' },
  { value: 'moderators', label: 'Moderators' },
  { value: 'vips', label: 'VIPs' },
  { value: 'everyone', label: 'Everyone' }
];

const formatAliases = (aliases) => {
  return aliases.length <= 2 ? aliases.join(' and ') : aliases.slice(0, -1).join(', ') + ' and ' + aliases.slice(-1);
};

const Page = () => {
  useEffect(() => {
    document.title = `LsCo Software - Bot Commands`;
  }, []);

  const [value, setValue] = useState(0);
  const [searchQuery, setSearchQuery] = useState('');
  const [accessLevel, setAccessLevel] = useState('everyone');

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleSearch = (event) => {
    setSearchQuery(event.target.value);
  };

  const handleAccessChange = (event) => {
    setAccessLevel(event.target.value);
  };

  const selectedCommands = value === 0 ? commands.twitch : commands.spotify;

  const filteredRows = selectedCommands.filter((command) => {
    const commandName = command.Name.toLowerCase();
    const commandDescription = command.Description.toLowerCase();
    const query = searchQuery.toLowerCase().replace(/^!/, '');

    const hasAccess =
      accessLevel === 'everyone' ||
      (accessLevel === 'vips' && command.Access.Channel === 1) ||
      (accessLevel === 'moderators' && (command.Access.Channel === 2 || command.Access.Channel === 1)) ||
      (accessLevel === 'broadcaster' && (command.Access.Channel === 3 || command.Access.Channel === 2 || command.Access.Channel === 1)) ||
      (accessLevel === 'admins' && command.Access.Global === 1) ||
      (accessLevel === 'owners' && command.Access.Global === 2);

    return hasAccess && (commandName.includes(query) || commandDescription.includes(query));
  });

  const rows = filteredRows.map(({ Name, Aliases, Description, Access }) => {
    const permissions =
      Access.Global === 1
        ? 'Admin'
        : Access.Global === 2
        ? 'Owner'
        : Access.Channel === 0
        ? 'Everyone'
        : Access.Channel === 1
        ? 'VIPs, Moderators and Broadcaster'
        : Access.Channel === 2
        ? 'Moderators and Broadcaster'
        : Access.Channel === 3
        ? 'Broadcaster'
        : 'N/A';

    return {
      command: Name,
      aliases: Aliases.length ? formatAliases(Aliases) : 'N/A',
      description: Description,
      permissions: permissions
    };
  });

  rows.sort((a, b) => {
    const accessOrder = {
      Everyone: 1,
      'VIPs, Moderators and Broadcaster': 2,
      'Moderators and Broadcaster': 3,
      Broadcaster: 4,
      Admin: 5,
      Owner: 6
    };

    return accessOrder[a.permissions] - accessOrder[b.permissions];
  });

  return (
    <Grid container spacing={2} alignItems="center" style={{ paddingLeft: '5vw', paddingRight: '5vw' }}>
      <Grid item xs={12}>
        <Grid container alignItems="center" sx={{ mt: 5 }}>
          <Avatar alt="Icon" src="/favicon.ico" sx={{ width: '125px', height: '125px', mr: 2.5 }} />
          <Typography variant="h1" component="h1" sx={{ wordBreak: 'break-all' }}>
            All available Commands.
          </Typography>
        </Grid>
      </Grid>
      <Grid item xs={12} sm={6}>
        <ThemeProvider theme={darkTheme}>
          <TextField
            fullWidth
            id="search-commands"
            label="Search commands"
            type="text"
            variant="outlined"
            placeholder="e.g. ping"
            value={searchQuery}
            onChange={handleSearch}
          />
        </ThemeProvider>
      </Grid>
      <Grid item xs={12} sm={6}>
        <ThemeProvider theme={darkTheme}>
          <FormControl fullWidth id="outlined-basic" variant="outlined">
            <InputLabel id="access-label">Access</InputLabel>
            <Select label="Access" labelId="access-label" id="access-select" value={accessLevel} onChange={handleAccessChange}>
              {accessLevels.map(({ value, label }) => (
                <MenuItem key={value} value={value}>
                  {label}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </ThemeProvider>
      </Grid>
      <Grid item xs={12}>
        <Box sx={{ width: '100%', bgcolor: 'background.paper' }}>
          <Tabs value={value} onChange={handleChange} centered>
            <Tab label="Bot Commands" />
            <Tab label="Spotify Commands" />
          </Tabs>
        </Box>
      </Grid>
      <Grid item xs={12}>
        <TableContainer component={Paper}>
          <Table aria-label="customized table">
            <TableHead>
              <TableRow sx={{ fontSize: 15, textAlign: 'center' }}>
                <TableCell sx={{ fontSize: 15, textAlign: 'center' }}>Command</TableCell>
                <TableCell sx={{ fontSize: 15, textAlign: 'center' }}>Aliases</TableCell>
                <TableCell sx={{ fontSize: 15, textAlign: 'center' }}>Description</TableCell>
                <TableCell sx={{ fontSize: 15, textAlign: 'center' }}>Permissions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.length ? (
                rows.map((row) => (
                  <TableRow key={row.command} sx={{ fontSize: 15, textAlign: 'center' }}>
                    <TableCell sx={{ fontSize: 15, textAlign: 'center' }} component="th" scope="row">
                      {row.command}
                    </TableCell>
                    <TableCell sx={{ fontSize: 15, textAlign: 'center' }}>{row.aliases}</TableCell>
                    <TableCell sx={{ fontSize: 15, textAlign: 'center' }}>{row.description}</TableCell>
                    <TableCell sx={{ fontSize: 15, textAlign: 'center' }}>{row.permissions}</TableCell>
                  </TableRow>
                ))
              ) : (
                <TableRow sx={{ fontSize: 15, textAlign: 'center' }}>
                  <TableCell sx={{ fontSize: 15, textAlign: 'center' }}>No commands match that query.</TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </TableContainer>
      </Grid>
    </Grid>
  );
};

export default Page;
